<template>
  <div>
  	<my-header></my-header>
  	<div class="my-slider">
			<mt-swipe :auto="4000">
			  <mt-swipe-item>
			  	<span class="fa fa-3x fa-wechat"></span>
			  	<p>欢迎你来访问</p>
			  </mt-swipe-item>
			  <mt-swipe-item>
			  	<span class="fa fa-3x fa-qq"></span>
			  	<p>祝你玩得开心</p>
			  </mt-swipe-item>
			  <mt-swipe-item>
			  	<span class="fa fa-3x fa-star"></span>
			  	<p>别忘记给我点赞哦</p>
			  </mt-swipe-item>
			</mt-swipe>  		
  	</div>
    <router-view/>
  	<my-footer></my-footer>
  </div>
</template>

<script>
import myHeader from './components/myHeader.vue'
import myFooter from './components/myFooter.vue'
import router from './router'
export default {
  name: 'app',
  router,
  components:{
  	myHeader,
  	myFooter
  }
}
</script>

<style lang="less" scoped>

	.my-slider{
		padding-top: 60px;
		height: 200px;
		background-color: lightblue;
		.mint-swipe-item{
			display: flex !important;
			justify-content: center !important;
			align-items: center !important;
			flex-flow: column wrap !important;
			span{
				color: white;
			}
			p{
				color: white;
			}
		}
		.mint-swipe-item:nth-child(1){
			background: -webkit-gradient(linear, 100% 0, 0 0, from(#00d2ff), to(#928DAB));
			background: -webkit-linear-gradient(to right, #00d2ff, #928DAB);
			background: -moz-linear-gradient(to right, #00d2ff, #928DAB);
			background: -o-linear-gradient(to right, #00d2ff, #928DAB);
			background: linear-gradient(to right, #00d2ff, #928DAB);
		}
		.mint-swipe-item:nth-child(2){
			background: -webkit-gradient(linear, 100% 0, 0 0, from(#c21500), to(#ffc500));
			background: -webkit-linear-gradient(to right, #c21500, #ffc500);
			background: -moz-linear-gradient(to right, #c21500, #ffc500);
			background: -o-linear-gradient(to right, #c21500, #ffc500);
			background: linear-gradient(to right, #c21500, #ffc500);
		}		
		.mint-swipe-item:nth-child(3){
			background: -webkit-gradient(linear, 100% 0, 0 0, from(#ff00cc), to(#333399));
			background: -webkit-linear-gradient(to right, #ff00cc, #333399);
			background: -moz-linear-gradient(to right, #ff00cc, #333399);
			background: -o-linear-gradient(to right, #ff00cc, #333399);
			background: linear-gradient(to right, #ff00cc, #333399);
		}		
	}
</style>



