<template>
	<div class="my-joke">
		<h3>下拉刷新笑话</h3>
		<mt-loadmore :top-method="loadTop" ref="loadmore">
		  <ul>
		    <li v-for="item in jokes">{{ item }}</li>
		  </ul>
		</mt-loadmore>
	</div>
</template>

<script>
export default {
  name: 'my-joke',
  data(){
  	return{
  		jokes:[]
  	}
  },
  methods:{
  	loadTop(){
  		this.$http.get('http://666xueyuan.com:1080/joke')
		  .then((response) => {
		    setTimeout(()=>{
		    	var firstNum  = ~~(Math.random()*10);
		    	var secondNum = ~~(Math.random()*10)+10;
		    	this.jokes.splice(0,1,response.data.result.data[firstNum].content)
		    	this.jokes.splice(1,1,response.data.result.data[secondNum].content)
		    	this.$refs.loadmore.onTopLoaded();
		    },1000)
		  })
		  .catch((error) => {
		  		this.jokes.splice(0,1,"笑话未获取")
		  		this.jokes.splice(1,1,"")
		    	this.$refs.loadmore.onTopLoaded();
		  })
  	}
  },
  mounted(){
		this.$http.get('http://666xueyuan.com:1080/joke')
	  .then((response) => {
	  	var firstNum  = ~~(Math.random()*10);
	  	var secondNum = ~~(Math.random()*10)+10;
	  	this.jokes.push(response.data.result.data[firstNum].content)
	  	this.jokes.push(response.data.result.data[secondNum].content)
	  })
	  .catch((error) => {
	  	this.jokes.push("笑话未获取")
	  })  	
  }
}	
</script>

<style lang="less" scoped>
	.my-joke{
		h3{
			margin: 5px 0 0;
			text-align: center;
			font-weight: 200;
		}
		ul,li{
			padding: 10px 10px;
			list-style: none;
			font-size: 0.9em;
		}		
		li:nth-child(n+2){
			border-top: 1px solid gray;
		}
	}
	
	
</style>


