<template>
	<div class="my-header">
		<h3>超级娱乐站</h3>
		<span @click="headerAlert()" class="fa fa-calendar"></span>
	</div>
</template>

<script>
	import Mint from 'mint-ui'
	export default{
		name:"my-header",
		data(){
			return{
			}
		},
		methods:{
			headerAlert(){
				Mint.MessageBox.confirm("你愿意给我评分吗", "感谢你");
			}
		}
	}
</script>

<style scoped lang="less">
	.my-header{
		position: fixed;
		width: 100%;
		left: 0;
		top: 0;
		height: 60px;
		background-color: #3374DE;
		h3{
			margin: 0px;
			height: 60px;
			line-height: 60px;
			font-weight: 200;
			text-align: center;
			color: white;
		}
		span{
			display: inline-block;
			position: absolute;
			top: 0;
			right: 10px;
			color: white;
			height: 60px;
			line-height: 60px;
			font-size: 1.5em;
		}
		.my-actionsheet{
			z-index: 5;
		}
	}
</style>


