<template>
	<div class="my-news">
		<h3>下拉刷新新闻</h3>
		<div class="my-news-button">
			<mt-button @click="sportsNews" size="normal" type="primary">体育新闻</mt-button>
			<mt-button @click="techNews"   size="normal" type="danger" >科技新闻</mt-button>
		</div>
		<mt-loadmore :top-method="loadTop" ref="loadmore">
			<a v-for="(item,index) in news" :href="item.url"><mt-cell :title="item.title"></mt-cell></a>			
		</mt-loadmore>
	</div>
</template>
<script>
export default {
  name: 'my-news',
  data(){
  	return{
  		news:[],
  		type:"体育"
  	}
  },
  methods:{
  	sportsNews(){
  		this.type = "体育";
   		this.ajax("体育");
  	},
   	techNews(){
  		this.type = "科技";
   		this.ajax("科技");
  	}, 	
  	ajax(title){
			this.$http.get('http://666xueyuan.com:1080/news&channel='+title)
		  .then((response) => {
			  	this.news = [];
			  	this.news.push({title:response.data.result.list[0].title,url:response.data.result.list[0].url})
			  	this.news.push({title:response.data.result.list[1].title,url:response.data.result.list[0].url})
			  	this.news.push({title:response.data.result.list[2].title,url:response.data.result.list[0].url})
		  })
		  .catch((error) => {
		  })  		
  	},
  	loadTop(){
			this.$http.get('http://666xueyuan.com:1080/news&channel='+this.type)
		  .then((response) => {
		  	setTimeout(()=>{
			  	var num = Math.ceil(Math.random()*8);
			  	this.news = [];
			  	this.news.push({title:response.data.result.list[num-1].title,url:response.data.result.list[0].url})
			  	this.news.push({title:response.data.result.list[num].title,url:response.data.result.list[0].url})
			  	this.news.push({title:response.data.result.list[num+1].title,url:response.data.result.list[0].url})
		    	this.$refs.loadmore.onTopLoaded();
		  	},500)
		  })
		  .catch((error) => {
		  })    		
  		
  	}
  },
  mounted(){
		this.$http.get('http://666xueyuan.com:1080/news&channel=体育')
	  .then((response) => {
	  	this.news.push({title:response.data.result.list[0].title,url:response.data.result.list[0].url})
	  	this.news.push({title:response.data.result.list[1].title,url:response.data.result.list[0].url})
	  	this.news.push({title:response.data.result.list[2].title,url:response.data.result.list[0].url})
	  })
	  .catch((error) => {
	  })  	
  }
}	
</script>
<style lang="less" scoped>
	.my-news{
		h3{
			margin: 5px 0 5px;
			text-align: center;
			font-weight: 200;
		}
		.my-news-button{
			display: flex;
			flex-flow: row nowrap;
			justify-content: space-around;
		}
		a{
			text-decoration: none;
			color: #555;
		}
	}
</style>


