<template>
	<div class="my-novel">
		<h3>下拉刷新漫画</h3>
		<mt-loadmore :top-method="loadTop" ref="loadmore">
			<img :src="novel.coverImg" alt="" />
			<p>{{novel.name}}</p>
		</mt-loadmore>
	</div>
</template>
<script>
export default {
  name: 'my-novel',
  data(){
  	return{
  		novel:{
  			name:"",
  			coverImg:""
  		}
  	}
  },
  methods:{
  	loadTop(){
  		this.$http.get('http://666xueyuan.com:1080/comic')
		  .then((response) => {
		  	console.log(response)
		    setTimeout(()=>{
			  	var num = ~~(Math.random()*20);
			  	this.novel.name     = response.data.result.bookList[num].name;
			  	this.novel.coverImg = response.data.result.bookList[num].coverImg;
			  	this.$refs.loadmore.onTopLoaded();
		    },1000)
		  })
		  .catch((error) => {
		    	this.$refs.loadmore.onTopLoaded();
		  })
  	}
  },
  mounted(){
		this.$http.get('http://666xueyuan.com:1080/comic')
	  .then((response) => {
	  	var num = ~~(Math.random()*20);
	  	this.novel.name     = response.data.result.bookList[num].name;
	  	this.novel.coverImg = response.data.result.bookList[num].coverImg;
	  })
	  .catch((error) => {
	  })  	
  }
}	
</script>
<style lang="less" scoped>
	.my-novel{
		text-align: center;
		h3{
			margin: 5px 0 0;
			text-align: center;
			font-weight: 200;
		}
		img{
			border-radius: 10px;
			padding: 5px;
			border: 1px solid gray;
			width: 50%;
		}
		p{
			text-align: center;
			margin: 4px;
		}
	}
</style>


