<template>
	<div class="my-footer">
		<router-link to="/joke"><div @click="changeActive(0)"><span :class="{abc:spanA}" class="fa fa-linux"></span><p>笑话</p></div></router-link>
		<router-link to="/novel"><div @click="changeActive(1)"><span :class="{abc:spanB}" class="fa fa-html5"></span><p>漫画</p></div></router-link>
		<router-link to="/news"><div @click="changeActive(2)"><span :class="{abc:spanC}" class="fa fa-css3"></span><p>新闻</p></div></router-link>
		<router-link to="/question"><div @click="changeActive(3)"><span :class="{abc:spanD}" class="fa fa-bell-o"></span><p>问答</p></div></router-link>
		<router-link to="/exam"><div @click="changeActive(4)"><span :class="{abc:spanE}" class="fa fa-file-text-o"></span><p>测试</p></div></router-link>
	</div>
</template>
<script>
	export default{
		name:"my-footer",
		data(){
			return{
				spanA:true,
				spanB:false,
				spanC:false,
				spanD:false,
				spanE:false
			}
		},
		methods:{
			changeActive(n){
				this.spanA = false;
				this.spanB = false;
				this.spanC = false;
				this.spanD = false;
				this.spanE = false;
				if(n==0) this.spanA = true;
				if(n==1) this.spanB = true;
				if(n==2) this.spanC = true;
				if(n==3) this.spanD = true;
				if(n==4) this.spanE = true;
			}
		}
		
		
	}
	
</script>

<style scoped lang="less">
	.my-footer{
		position: fixed;
		width: 100%;
		left: 0;
		bottom: 0;
		background-color: #3374DE;
		height: 60px;
		display: flex;
		flex-flow: row nowrap;
		a{
			width: 25%;
			text-decoration: none !important;
			.abc{
				color: #cc0 !important;
				p{
					color: #cc0 !important;
				}
			}
			div{
				display: flex;
				flex-flow: column nowrap;
				justify-content: center;
				align-items: center;
				height: 60px;
				color: white;
				p{
					margin: 3px 0 0;
					font-size: 0.8em;
				}
				span{
					font-size: 1.5em;
					font-weight: 200;
				}
			}			
		}
	}


</style>


