<template>
	<div class="my-question">
		<h3>我是问答{{q}}</h3>
		<mt-field label="提问" placeholder="请输入你的问题" v-model="q"></mt-field>
		<mt-button @click="ajax()" type="danger">提交</mt-button>
		<div>
			{{a}}
		</div>
	</div>
</template>

<script>
export default {
  name: 'my-question',
  data(){
  	return{
  		q:"",
  		a:""
  	}
  },
  methods:{
  	ajax(){
			this.$http.get('http://666xueyuan.com:1080/ai&question='+this.q)
		  .then((response) => {
		  	console.log(response.data.result.content)
		  	this.a = response.data.result.content;
		  })
		  .catch((error) => {
		  })   		
  	}
  }
}	
</script>

<style lang="less" scoped>
	.my-question{
		padding: 0px 10px;
		h3{
			margin: 5px 0 5px;
			text-align: center;
			font-weight: 200;
		}
	}
</style>


