<template>
	<div class="my-exam">
		<h3>欢迎参加测试</h3>
		<div class="my-exam-box">
			<mt-radio v-show="index==start" v-for="(exam,index) in exams" :title="exam.title" v-model="exam.choose" :options="exam.options" :key="index"></mt-radio>
		</div>
		<div class="my-exam-move">
			<mt-button v-show="start>0" @click="prev()" type="primary">上一题</mt-button>
			<mt-button v-show="start<2" @click="next()" type="primary">下一题</mt-button>
			<mt-button v-show="start==2" @click="submit()" type="primary">提交</mt-button>
		</div>
			
	</div>
</template>

<script>
export default {
  name: 'my-exam',
  data(){
  	return{
  		start:0,
  		exams:[
  			{	title:"1:请问你喜欢什么类型的姑娘?",
  				options:[
		 			  { label: '丰满型',value: 'A'},
					  { label: '性感型',value: 'B'},
					  { label: '美貌型',value: 'C'},
					  { label: '萝莉型',value: 'D'}	
  				],
  				chose:""
  			},
  			{	title:"2:请问你喜欢哪个城市?",
  				options:[
		 			  { label: '上海',value: 'A'},
					  { label: '北京',value: 'B'},
					  { label: '广州',value: 'C'},
					  { label: '武汉',value: 'D'}	
  				],
  				chose:""
  			},
  			{	title:"3:请问你喜欢什么类型的食物?",
  				options:[
		 			  { label: '火锅',  value: 'A'},
					  { label: '撸串',  value: 'B'},
					  { label: '小龙虾',value: 'C'},
					  { label: '家常菜',value: 'D'}	
  				],
  				chose:""
  			}
  		]
  	}
  },
  methods:{
  	prev(){
  		this.start--;
  	},
   	next(){
  		this.start++;
  	}, 	
  	submit(){
//		let finish = false;
  		this.exams.forEach((ele,index)=> {
  			if(ele.chose == "") this.$messagebox('有题目未完成，不能提交');
  		})
  	}
  },
  mounted(){
  }
}	
</script>

<style lang="less" scoped>
	.my-exam{
		h3{
			margin: 5px 0 5px;
			text-align: center;
			font-weight: 200;
		}
		.my-exam-box{
		}
		.my-exam-move{
			display: flex;
			padding: 10px 10px 60px;
			position: relative;
			button{
				display: inline-block;
				position: absolute;
			}
			button:first-child{
				left: 10px;
			}
			button:nth-child(2){
				right: 10px;
			}			
			button:last-child{
				right: 10px;
			}
		}
	}
</style>


